* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.login-container {
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  max-width: 100vw;
  padding: 12px;
  background-image: url("/dist/images/bg-login.jpg");
  background-position: center;
  background-size: cover;
  .nav-container {
    height: 15vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .nav-bar {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      height: 10vh;
      width: 50vw;
      border-radius: 50px;
      background-color: rgba(0, 0, 0, 0.452);
      .nav-item {
        display: flex;
        justify-content: center;
        align-items: center;
        a {
          list-style: none;
          text-decoration: none;
          font-size: 24px;
          color: white;
        }
      }
    }
  }
  .main-content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
    width: 90vw;
    border-radius: 12px;
    padding: 16px;
    background-color: rgba(224, 224, 224, 0.815);
  }
}
